.home-box {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 80%;
    width: 80%;
    align-items: center;
    min-width: 80%;
    border: 1px solid lightsalmon;
    margin: 0 auto;
}

.form-box {
    display: block;
    justify-content: space-between;
    box-sizing: border-box;
    min-width: 400px;
}

.login-field {
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
    min-width: 200px;
    width: 33%;
    min-width: 200px;
    float: left;
}

.signup-bt {
    display: block;
    flex-wrap: wrap;
    min-width: 200px;

}